.homeBackgroundContainer{
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;

    background: -webkit-linear-gradient(44deg, #9587e5, #57bbc5, #879ee8, #5db1b9);

    background: -o-linear-gradient(44deg, #9587e5, #57bbc5, #879ee8, #5db1b9);

    background: linear-gradient(46deg, #9587e5, #57bbc5, #879ee8, #5db1b9);
    background-size: 800% 800%;

    -webkit-animation: gradientBackground 15s ease infinite;
    animation: gradientBackground 15s ease infinite;
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
@-webkit-keyframes gradientBackground {
    0%{background-position:0% 94%}
    50%{background-position:100% 7%}
    100%{background-position:0% 94%}
}
@keyframes gradientBackground {
    0%{background-position:0% 94%}
    50%{background-position:100% 7%}
    100%{background-position:0% 94%}
}


